      "DisplayName": "AIDocStudyPurpose",
      "OriginalValue": "",
      "UserValue": "\"This study will be done to see if a new medicine called TB006 can help people who have had a mild to moderate stroke. The researchers want to know if this medicine is safe and if it can help people get better after their stroke. They will give the medicine to some people and a fake medicine (placebo) to others to compare the results. They will also check for any side effects and how the medicine moves through the body , .\"",
