      "UserValue": "\"During the study, doctors will give some people a special medicine and others a fake medicine (called a placebo) to see if the special medicine helps people who had a stroke. They will check how well people can move and think before and after taking the medicine. They will also look at their heart and blood to make sure the medicine is safe. The doctors will compare the results to see if the special medicine helps people get better from their stroke .\"",
