      "ComponentKeyDataAccess": "READ-WRITE",
      "RegistryKeyID": 0,
      "Name": "PubWhatSummaryAbout",
      "DisplayName": "PubWhatSummaryAbout",
      "OriginalValue": "",
      "UserValue": "\"The summary is about a special computer program called the Dual Transformer Encoder (DTE). In the future, this program will help other computer programs understand and compare sentences better. It will also help find sentences that mean the same thing. This will make it easier for computers to answer questions and understand what people are saying.\"",
      "EditorValue": "\"The summary is about a special computer program called the Dual Transformer Encoder (DTE). In the future, this program will help other computer programs understand and compare sentences better. It will also help find sentences that mean the same thing. This will make it easier for computers to answer questions and understand what people are saying.\"",
