      "ComponentKeyDataAccessID": 3,
      "ComponentKeyDataType": "TEXT",
      "ComponentKeyDataAccess": "READ-WRITE",
      "RegistryKeyID": 0,
      "Name": "PubWhyResearchNeeded",
      "DisplayName": "PubWhyResearchNeeded",
      "OriginalValue": "",
      "UserValue": "\"The purpose of this study will be to test a new medicine called TB006 in people who have had a type of stroke called an AIS event. The study will help doctors see if TB006 can help these people get better after their stroke .\\n\\nThis study will be needed because strokes can cause serious problems, like making it hard for people to move or talk. Right now, there are not many treatments that work well for everyone. By testing TB006, scientists hope to find a new way to help more people recover from strokes and live better lives .\"",
      "EditorValue": "\"The purpose of this study will be to test a new medicine called TB006 in people who have had a type of stroke called an AIS event. The study will help doctors see if TB006 can help these people get better after their stroke .\\n\\nThis study will be needed because strokes can cause serious problems, like making it hard for people to move or talk. Right now, there are not many treatments that work well for everyone. By testing TB006, scientists hope to find a new way to help more people recover from strokes and live better lives .\"",
